* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: #0090f0;
    padding: 20px;
    font-family: "Karla", sans-serif;
    width: 100vw;
    height: 100vh;
}

main {
    background-color: #ececec;
    min-height: 400px;
    max-width: 800px;
    border-radius: 5px;
    box-shadow: 0 0 20px black;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: auto auto;
    position: relative;
}

.title {
    color: #0090f0;
    font-size: 5em;
    margin: 0;
    text-shadow: 1px 1px 1px #919191, 1px 2px 1px #919191, 1px 3px 1px #919191,
        1px 4px 1px #919191, 1px 5px 1px #919191, 1px 6px 1px #919191,
        1px 7px 1px #919191, 1px 8px 1px #919191, 1px 9px 1px #919191,
        1px 10px 1px #919191, 1px 18px 6px rgba(16, 16, 16, 0.4),
        1px 22px 10px rgba(16, 16, 16, 0.2), 1px 25px 35px rgba(16, 16, 16, 0.2),
        1px 30px 60px rgba(16, 16, 16, 0.4);
}

.instructions {
    font-family: "Inter", sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
    width: 50%;
}

.style-selection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.style-selection--header {
    margin: 0;
}

.style-selection--buttons {
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
    justify-content: center;
    align-items: center;
    margin: 10px 0 5px 0;
}

.style-selection--imgBtn {
    width: 100px;
    border-radius: 0.3rem;
    border: 2px solid grey;
    cursor: pointer;
    opacity: 0.5;
}

.style-selection--imgBtn:hover {
    transform: scale(1.2);
    opacity: 1;
    border: 2px solid #0090f0;
    box-shadow: 0 0 5px #0090f0;
    z-index: 1;
}

.gameWindow {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.dice-container {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 20px;
    margin-bottom: 40px;
}

.die-face {
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.die-num {
    font-size: 2rem;
}

.btn {
    height: 100px;
    width: 200px;
    border: none;
    border-radius: 6px;
    background-color: #0090f0;
    color: white;
    font-size: 1.2rem;
    font-family: "Karla", sans-serif;
    cursor: pointer;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.9);
}

.menuWindow {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
}

.user-name--container {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    border-radius: 5px;
    z-index: 2;
}

#user {
    width: 170px;
    font-size: 1.5rem;
    font-weight: 700;
    font-style: italic;
    background-color: #94d4ff;
    margin-bottom: 50px;
    padding: 5px;
    border-radius: 0.5rem;
}

.stopwatch {
    position: absolute;
    top: 5px;
    left: 5px;
}

.scoreTablesContainer {
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
}

.scoretable {
    margin-top: 2rem;
    padding: 1rem;
    border: 2px solid #0090f0;
    border-radius: 1rem;
    box-shadow: 0 0 10px #0090f0;
    background-color: #b3b3b3;
}

.scoretable--header {
    margin: 0;
    padding: 0;
}

.scoretable--scores-container {
    max-height: 95px;
    overflow: auto;
}

.scoretable--score {
    margin: 0;
    padding: 0;
}

.winPopup {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 1;
}

.winpopup--content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.winpopup--text {
    font-size: 2rem;
    color: white;
}
.winpopup--fouls {
    font-size: 1rem;
    color: white;
}

.winpopup--time {
    font-size: 2rem;
    color: white;
}

.missed-dice-text {
    color: red;
}

.missed-dice-bg {
    background-color: red;
}

@media screen and (max-width: 600px) {
    .scoreTablesContainer {
        flex-direction: column;
        row-gap: 1rem;
    }
}
